.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  font-family: "IBM Plex Sans Arabic", sans-serif;
  margin: 0;
}
.container {
  width: 100vw;
  height: 100vh;
  justify-content: space-around;
}
.container h2 {
  text-align: center;
}
.myform {
  margin: 50px auto;
  width: 50%;
  height: 40%;
  background-color: antiquewhite;
  padding-top: 10px;
  padding-left: 5px;
  padding-right: 5px;
  padding-bottom: 5px;
  border-radius: 5px;
}
.form-field {
  width: 100%;
  padding: 14px 0px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.subButton {
  display: block;
  position: relative;
  top: 10px;
  width: 75px;
  height: 30px;
  margin: auto;
  background-color: #0e5ba4;
  border: none;
  border-radius: 5px;
  color: aliceblue;
  cursor: pointer;
}
